.Slideshow {
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.containerSlide {
  width: 35em;
  margin: auto;
  border: solid black;
  border-radius: 1em;
  margin-bottom: 1em;
}
.active {
  margin: auto;
}
.each-slide {
  width: 25em;
  margin: auto;
}
.containerSlide img {
  width: 25em;
}

.slideshow, .slideshowMedium, .slideshowSmall {
  margin: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
#reference
{
  font-size: 0.8em;
  font-family: 'Times New Roman', Times, serif;
  text-align: left;
  
}

@media (max-width: 1024px) {
  .containerSlide {
    width: 90vw;
  }
}
